<template>
  <div class="dd-login-view">
    <div class="remind">正在校验用户信息...</div>
  </div>
</template>
<script>
export default {
  name: "ddLogin",
  data() {
    return {
      corpId: this.$route.query.corpId,
    };
  },
  created() {
    this.initDd();
  },
  methods: {
    initDd() {
      let init = new Promise((resolve, reject) => {
        var el = document.createElement("script");
        el.src = "https://g.alicdn.com/dingding/dingtalk-jsapi/2.10.3/dingtalk.open.js";
        document.body.appendChild(el);
        el.onload = function () {
          resolve();
        };
        el.onerror = function () {
          reject();
        };
      });
      return init.then(() => {
        const { corpId } = this;
        if (!corpId) return dsf.layer.message("当前企业corpId为空", false);
        return this.authentication(corpId);
      });
    },
    initDdConfig() {
      const { corpId } = this;
      return new Promise((resolve, reject) => {
        const sourceUrl = "/api/ding/publics/config";
        this.$http
          .get(sourceUrl, { corpId: corpId })
          .done(res => {
            if (res.success) {
              resolve(res);
            } else {
              reject(res);
            }
          })
          .error(error => {
            reject(error);
          });
      });
    },
    ddLogin(code) {
      const loginUrl = "login/ding";
      const params = {
        code: code,
        corpId: this.corpId,
      };
      this.$http
        .post(loginUrl, params)
        .done(res => {
          if (res.success) {
            const path = dsf.client.android() || dsf.client.iOS() ? dsf.config.setting_public_mobileindex_url : dsf.config.setting_public_pcindex_url;
            dsf.cookies.set("isLogin", 1);
            location.href = path;
          } else {
            dsf.layer.message("自动登录:" + res.message, false);
          }
        })
        .error(error => {
          dsf.layer.message("自动登录:" + err.message, false);
        });
    },
    authentication() {
      let that = this;
      const { corpId } = this;
      if (dd.env.platform !== "notInDingTalk") {
        this.initDdConfig()
          .then(res => {
            dd.runtime.permission.requestAuthCode({
              corpId: corpId,
              onSuccess: res => {
                that.ddLogin(res.code);
              },
              onFail: err => {
                dsf.layer.message("获取authCode失败");
              },
            });
          })
          .catch(err => {
            dsf.layer.message("API：/api/ding/publics/config" + err.message, false);
          });
      } else {
        console.log("notInDingTalk");
      }
    },
    checkPlatfrom() {
      var u = navigator.userAgent,
        app = navigator.appVersion;
      return {
        trident: u.indexOf("Trident") > -1, //IE内核
        presto: u.indexOf("Presto") > -1, //opera内核
        webKit: u.indexOf("AppleWebKit") > -1, //苹果、谷歌内核
        gecko: u.indexOf("Gecko") > -1 && u.indexOf("KHTML") == -1, //火狐内核
        mobile: !!u.match(/AppleWebKit.*Mobile.*/), //是否为移动终端
        ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios终端
        android: u.indexOf("Android") > -1 || u.indexOf("Adr") > -1, //android终端
        iPhone: u.indexOf("iPhone") > -1, //是否为iPhone或者QQHD浏览器
        iPad: u.indexOf("iPad") > -1, //是否iPad
        webApp: u.indexOf("Safari") == -1, //是否web应该程序，没有头部与底部
        weixin: u.indexOf("MicroMessenger") > -1, //是否微信
        qq: u.match(/\sQQ/i) == " qq", //是否QQ
      };
    },
  },
};
</script>
<style lang="scss">
.dd-login-view {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  .remind {
    font-size: 16px;
    color: #333;
  }
}
</style>
