<template>
  <div id="app">
    <home :page-suffix="pageSuffix"></home>
  </div>
</template>

<script>
import home from "./views/Home";
export default {
  components: {
    home,
  },
  provide() {
    return {
      $app: this,
    };
  },
  data() {
    return {
      pageSuffix: "else",
    };
  },
  created() {
    this.init();
    let htmlName = window.location.pathname;
    let route = ["_project", "_product", ""];
    let pageSuffix = "else";
    for (let i = 0; i < route.length; i++) {
      if (htmlName.indexOf(`/index${route[i]}.html`) >= 0) {
        pageSuffix = "index";
        break;
      } else if (htmlName.indexOf(`/index1${route[i]}.html`) >= 0) {
        pageSuffix = "index1";
        break;
      } else if (htmlName.indexOf(`/ncIndex${route[i]}.html`) >= 0) {
        pageSuffix = "ncIndex";
        break;
      } else {
        pageSuffix = "else";
      }
    }
    this.pageSuffix = pageSuffix;
  },
  mounted() {},
  methods: {
    init() {
      const ua = navigator.userAgent;
      if (ua.toLowerCase().indexOf("dingtalk") > -1) {
        var el = document.createElement("script");
        el.src = "https://g.alicdn.com/dingding/dingtalk-jsapi/2.10.3/dingtalk.open.js";
        document.body.appendChild(el);
        el.onload = function () {};
        el.onerror = function () {};
        dsf.cookies.set("isDd", 1);
      }
      dsf.cookies.set("checkDd", navigator.userAgent);
    },
  },
};
</script>
